.header {
  position: fixed;
  width: 100%;
  background-color: var(--bg-black-color);
  height: 56px;
  padding: calc(var(--gap) * 0.5) 0;
  z-index: 100;

  &__container {
    padding-left: calc(var(--gap) * 2.5);
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__logo {
    width: 146px;
    height: 25px;
    margin-right: 82px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  &__nav {
    display: flex;
  }

  &__nav-link {
    display: block;
    padding: var(--gap) calc(var(--gap) * 2);
    color: var(--white-color);
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
  }

  &__aside {
    margin-left: auto;
  }

  &__user {
    display: flex;
    align-items: center;
  }

  &__user-photo {
    width: 32px;
    height: 32px;
    border-radius: var(--border-radius-small);
    overflow: hidden;
    flex: 0 0 auto;
    margin-right: var(--gap);

    img {
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
    }
  }

  &__user-name {
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: var(--white-color);
  }

  &__user-position {
    font-weight: 500;
    font-size: 13px;
    line-height: 16px;
    color: var(--text-light-white-color);
  }
}
