.main-slider {
  position: relative;
  width: 100%;
  background-color: var(--white-color);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-bottom: calc(var(--gap) * 4);
  &__list {
    padding: 6px 0;
  }

  &__item {
    display: flex !important;
    overflow: hidden;
  }

  &__img {
    width: 357px;
    height: 274px;
    flex: 0 0 auto;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: calc(var(--gap) * 4.5);
  }

  &__title {
    font-family: var(--font-raleway);
    font-weight: 800;
    font-size: 40px;
    line-height: 47px;
    margin-bottom: calc(var(--gap) * 1.5);

    span {
      color: var(--main-color);
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
  }

  &__navigation {
    display: flex;
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    bottom: 12px;
  }

  &__arrow {
    width: 24px;
    height: 24px;
    flex: 0 0 auto;
  }

  .slick-dots {
    display: flex;

    li {
      position: relative;
      display: block;
      width: 24px;
      height: 24px;

      &.slick-active {
        button {
          background-color: var(--main-color);
        }
      }

      button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: var(--light-gray-color);
        color: transparent;
        transition: background-color 0.3s;
      }
    }
  }
}
